___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin:0;
    padding:0;

}
html {
    height: 100%;;
}
body {
    display:grid;
    min-height: 100%;
    grid-template-rows: 100px 1fr;
    grid-template-areas: 
    'header'
    'main';
}
header {

    grid-area: header;
    background: rgb(241, 230, 230);
    color:#70686c ;
    border: 1px;
    text-align: center;
    padding-top:20px;
}
main {

    /* grid-area:main; */
    background: #7f8c8d ;
    border: 1px dashed red;
}
#content {

    height:100%;

}
#content-container {
    display:grid;
    grid-template-columns: 250px 250px 250px;
    gap:15px;
    margin-top: 10px;
    padding: 15px;
}
.todo-card {

    border: none;
    border-radius: 15px;
    min-height:150px;
    background: #b0b2b3;
    color:white;
    font-size:20px;
    padding:10px;
}
.btn-container {

    display:flex;
    justify-content: space-around;
}
.btn {

    min-width: 40px;
    width:fit-content;
    border-radius: 10px;
    padding: 5px 10px 5px 10px;
    color:white;
    margin: 10px;
    border:none;
}
#del_btn {
    background: #ED0800 ;
}
#edit_btn {
    background: #5783db;
}
#complete_btn {

    background: #5dbea3;
}
#add_btn {
    background: #424a93 ;
}
#submit_btn{
    background: #5dbea3;
}
select {
    margin-left:5px;
}
form {

    border: 1px;
    background: #e5e7e9 ;
    padding:10px;
    border-radius:15px;
    width:300px;
    margin: 10px;
}
input, textarea {

    width:100%;
    box-sizing: border-box;
    border:none;
    margin-top:5px;
    margin-bottom:5px;
    color:black;
}
textarea {
    resize: none;
}
.title-div,.description-div, .duedate-div,.priority-div {

    margin:5px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,QAAQ;IACR,SAAS;;AAEb;AACA;IACI,YAAY;AAChB;AACA;IACI,YAAY;IACZ,gBAAgB;IAChB,6BAA6B;IAC7B;;UAEM;AACV;AACA;;IAEI,iBAAiB;IACjB,8BAA8B;IAC9B,cAAc;IACd,WAAW;IACX,kBAAkB;IAClB,gBAAgB;AACpB;AACA;;IAEI,oBAAoB;IACpB,oBAAoB;IACpB,sBAAsB;AAC1B;AACA;;IAEI,WAAW;;AAEf;AACA;IACI,YAAY;IACZ,wCAAwC;IACxC,QAAQ;IACR,gBAAgB;IAChB,aAAa;AACjB;AACA;;IAEI,YAAY;IACZ,mBAAmB;IACnB,gBAAgB;IAChB,mBAAmB;IACnB,WAAW;IACX,cAAc;IACd,YAAY;AAChB;AACA;;IAEI,YAAY;IACZ,6BAA6B;AACjC;AACA;;IAEI,eAAe;IACf,iBAAiB;IACjB,mBAAmB;IACnB,0BAA0B;IAC1B,WAAW;IACX,YAAY;IACZ,WAAW;AACf;AACA;IACI,oBAAoB;AACxB;AACA;IACI,mBAAmB;AACvB;AACA;;IAEI,mBAAmB;AACvB;AACA;IACI,oBAAoB;AACxB;AACA;IACI,mBAAmB;AACvB;AACA;IACI,eAAe;AACnB;AACA;;IAEI,WAAW;IACX,oBAAoB;IACpB,YAAY;IACZ,kBAAkB;IAClB,WAAW;IACX,YAAY;AAChB;AACA;;IAEI,UAAU;IACV,sBAAsB;IACtB,WAAW;IACX,cAAc;IACd,iBAAiB;IACjB,WAAW;AACf;AACA;IACI,YAAY;AAChB;AACA;;IAEI,UAAU;AACd","sourcesContent":["* {\n    margin:0;\n    padding:0;\n\n}\nhtml {\n    height: 100%;;\n}\nbody {\n    display:grid;\n    min-height: 100%;\n    grid-template-rows: 100px 1fr;\n    grid-template-areas: \n    'header'\n    'main';\n}\nheader {\n\n    grid-area: header;\n    background: rgb(241, 230, 230);\n    color:#70686c ;\n    border: 1px;\n    text-align: center;\n    padding-top:20px;\n}\nmain {\n\n    /* grid-area:main; */\n    background: #7f8c8d ;\n    border: 1px dashed red;\n}\n#content {\n\n    height:100%;\n\n}\n#content-container {\n    display:grid;\n    grid-template-columns: 250px 250px 250px;\n    gap:15px;\n    margin-top: 10px;\n    padding: 15px;\n}\n.todo-card {\n\n    border: none;\n    border-radius: 15px;\n    min-height:150px;\n    background: #b0b2b3;\n    color:white;\n    font-size:20px;\n    padding:10px;\n}\n.btn-container {\n\n    display:flex;\n    justify-content: space-around;\n}\n.btn {\n\n    min-width: 40px;\n    width:fit-content;\n    border-radius: 10px;\n    padding: 5px 10px 5px